* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.board {
  margin: 20px;
  background-color: #eeeeee;
}

.dot {
  height: 35px;
  width: 35px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
}

.bdot {
  height: 35px;
  width: 35px;
  background-color: #bbb;
  border-radius: 50%;
  border: solid 2px black;
  display: inline-block;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
}

.smalldot {
  height: 15px;
  width: 15px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
  margin-top: 15px;
  margin-bottom: 15px;
  margin-left: 3px;
}

.button {
  width: 100px;
  margin-top: 5px;
  margin-left: 15px;
  margin-bottom: 5px;
  background-color: #999999;
  color: #222222;
  display: grid;
  gap: 10px;
}

h4 {
  text-align: center;
  margin-top: 5px;
}

.slottery {
  width: 210px;
  margin-top: 70px;
  margin-left: 200px;
  background-color: #333333;
  display: grid;
  gap: 5px;
}
@media screen and (min-width: 200px) {
  .slottery {
    grid-template-columns: repeat(4, 1fr);
  }
}

.menu {
  width: 120px;
  margin-top: -400px;
  margin-left: 62px;
  background-color: #dddddd;
  display: grid;
  gap: 10px;
}
@media screen and (min-width: 100px) {
  .menu {
    grid-template-columns: repeat(1, 1fr);
  }
}

.score {
  width: 120px;
  margin-top: 0px;
  margin-left: 62px;
  background-color: #dddddd;
  display: grid;
  gap: 10px;
}
@media screen and (min-width: 100px) {
  .score {
    grid-template-columns: repeat(1, 1fr);
  }
}

.colorPalette {
  width: 45px;
  margin-top: 14px;
  margin-left: 100px;
  background-color: #dddddd;
  display: grid;
  gap: 1px;
}
@media screen and (min-width: 40px) {
  .colorPalette {
    grid-template-columns: repeat(1, 1fr);
  }
}

.match {
  width: 120px;
  margin-top: -395px;
  margin-left: 430px;
  background-color: #dddddd;
  display: grid;
  row-gap: 5px;
  column-gap: 10px;
}
@media screen and (min-width: 120px) {
  .match {
    grid-template-columns: repeat(4, 1fr);
  }
}

.header {
  margin-top: -450px;
  margin-left: 230px;
}
@media screen and (min-width: 200px) {
  .header {
    grid-template-columns: repeat(1, 1fr);
  }
}
